import React from "react"
import Img from "gatsby-image"
import { Link, SwipeLink } from "./link"
import styled, { css } from "styled-components"
import posed, { PoseGroup } from "react-pose"
import { TransitionState } from "gatsby-plugin-transition-link"

import closeIcon from "../images/icons/close.svg"
import previousIcon from "../images/icons/previous.svg"
import nextIcon from "../images/icons/next.svg"
import Layout from "./layout"
import "react-responsive-carousel/lib/styles/carousel.min.css"
import { Carousel } from "react-responsive-carousel"

export const pageQuery = graphql`
  query PostQuery($name: String) {
    illuminatorDataJson(name: { eq: $name }) {
      name
      title
      size
      year
      description
      previousItemName
      nextItemName
      images {
        fluid(quality: 100) {
          originalName
          ...GatsbyImageSharpFluid_tracedSVG
        }
      }
    }
  }
`

const FadeBox = posed.div({
  visible: { opacity: 1 },
  hidden: { opacity: 0 },
})

const Container = styled(FadeBox)`
  min-height: 100vh;
  max-width: 1400px;
  gap: 30px;
  justify-items: center;
  align-items: center;
  margin: 0 auto;
  display: grid;
  @media (max-width: 1400px) {
    display: flex;
    flex-direction: column;
  }
  @media (max-width: 750px) {
  }
`

const StyledCarousel = styled(Carousel)`
  background-color: #d2d2d2;
  box-sizing: border-box;
  width: 920px;
  @media (max-width: 1400px) {
    width: 100vw;
  }
`

const Info = styled.div`
  background-color: white;
  grid-column: 2;
  grid-row: 1;
  height: 850px;
  width: 450px;
  padding: 30px;
  box-sizing: border-box;
  font-size: 0.9rem;
  line-height: 160%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  overflow-y: auto;
  @media (max-width: 1400px) {
    grid-column: 1;
    grid-row: 2;
    width: 100vw;    
    height: unset;
  }
  @media (max-width: 750px) {
  }
`
const Title = styled.div`
  font-weight: bold;
`
const Description = styled.div``
const Size = styled.span`
  font-weight: 100;
`
const CloseButton = styled.div`
  position: fixed;
  top: 20px;
  right: 20px;
`
const NextButton = styled.div`
  position: fixed;
  bottom: 20px;
  right: 20px;
`
const PreviousButton = styled.div`
  position: fixed;
  bottom: 20px;
  right: 80px;
`

const Indicator = styled.li`
  background-color: #d2d2d2;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  display: inline-block;
  margin: 0 8px;
  cursor: pointer;
  ${({isSelected}) => isSelected && css`
    background-color: #1A1A1A;
  `}
`

function CompareImages(a, b) {
  function GetWeight(imageName) {
    if (imageName.endsWith("-clip.png")) return 1
    if (imageName.endsWith("-crop.png")) return 2
    if (imageName.endsWith("-interior.png")) return 3
    return 0
  }
  return GetWeight(a.fluid.originalName) - GetWeight(b.fluid.originalName)
}

function RenderIndicator(onClickHandler, isSelected, index, label) {
  return <Indicator onClick={onClickHandler} isSelected={isSelected} />
}

export default props => {
  const data = props.data.illuminatorDataJson
  if (!data || !data.images) return null
  const formattedDescription = data.description
    .split(`\n\n`)
    .map(paragraph => `<p>${paragraph.replace(/\n/g, `<br>`)}</p>`)
    .join(``)
  return (
    <Layout>
      <Container>
        <TransitionState>
          {({ mount, transitionStatus }) => {
            const pose = mount ? "visible" : "hidden"
            return (
              <PoseGroup pose={pose}>
                <StyledCarousel
                  key="carousel"
                  infiniteLoop
                  autoPlay
                  showThumbs={false}
                  showStatus={false}
                  renderIndicator={RenderIndicator}
                >
                  {data.images.sort(CompareImages).map((image, index) => (
                    <Img
                      fluid={image.fluid}
                      style={{ height: "100%", maxHeight: "850px", backgroundColor: "white" }}
                      imgStyle={{
                        backgroundColor: "white",
                        objectFit: "contain",
                        border: "15px solid white",
                      }}
                    />
                  ))}
                </StyledCarousel>
                <Info key="info">
                  <Title>
                    {data.title} {data.year} <Size>({data.size})</Size>
                  </Title>
                  <Description
                    dangerouslySetInnerHTML={{ __html: formattedDescription }}
                  />
                </Info>
                <CloseButton key="closeButton">
                  <Link to={"illuminator/"}>
                    <img alt="languageIcon" src={closeIcon} />
                  </Link>
                </CloseButton>
                <PreviousButton key="previousButton">
                  <SwipeLink
                    direction="right"
                    to={"illuminator/" + data.previousItemName}
                  >
                    <img alt="languageIcon" src={previousIcon} />
                  </SwipeLink>
                </PreviousButton>
                <NextButton key="nextButton">
                  <SwipeLink
                    direction="left"
                    to={"illuminator/" + data.nextItemName}
                  >
                    <img alt="languageIcon" src={nextIcon} />
                  </SwipeLink>
                </NextButton>
              </PoseGroup>
            )
          }}
        </TransitionState>
      </Container>
    </Layout>
  )
}
